<template>
  <div>
    <form method="post"
      novalidate>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('name')}">
          <input v-validate="'required|alpha_spaces|min:3'" name="name" id="name" class="mdl-textfield__input" v-model="productParams.name">
          <label class="mdl-textfield__label" for="name">Name</label>
          <span class="mdl-textfield__error">{{ errors.first('name') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('price')}">
          <input v-validate="'required|integer'" name="price" id="price" class="mdl-textfield__input" v-model="productParams.price">
          <label class="mdl-textfield__label" for="price">Price</label>
          <span class="mdl-textfield__error">{{ errors.first('price') }}</span>
        </div>
      </div>
      <div>
        <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('quantity')}">
          <input v-validate="'required|integer'" name="quantity" id="quantity" class="mdl-textfield__input" v-model="productParams.quantity">
          <label class="mdl-textfield__label" for="quantity">Quantity</label>
          <span class="mdl-textfield__error">{{ errors.first('quantity') }}</span>
        </div>
      </div>
      <div>
        <autocomplete
          :data="rawMaterials.data"
          :action="fetchRawMaterials"
          :field="'name'"
          :multiSelect="true"
          :placeholder="'Select raw materials'"
          @selected="addRawMaterial"></autocomplete>
      </div>
    </form>
  </div>
</template>
<script>
import { Autocomplete } from 'SHARED/autocomplete'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'product-form',
  components: {
    Autocomplete
  },
  props: {
    product: Object
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  data () {
    return {
      params: {
        name: '',
        quantity: '',
        raw_materials: [],
        price: ''
      }
    }
  },
  computed: {
    productParams: {
      get () {
        return this.product || this.params
      }
    },
    ...mapGetters({
      'rawMaterials': 'getRawmaterials'
    })
  },
  methods: {
    ...mapActions([
      'fetchRawMaterials'
    ]),
    valid () {
      return this.$validator.validateAll()
    },
    addRawMaterial (material) {
      this.$set(this.params, 'raw_materials', material)
    }
  }
}
</script>



